  //    
  const handleButtonClick = (buttonName) => {
    setActiveButton(buttonName);
    if (buttonName === "button1") {
      navigate("/doing");
    } else if (buttonName === "button2") {
      navigate("/sponsor");
    } else if (buttonName === "button3") {
      navigate("/introduce");
    }
    onClose();
  };

